.task-list {
	margin: 0;
	padding: 0;
	list-style: none;
}

.task-list li {
	position: relative;
	font-size: 24px;
	border-bottom: 1px solid #ededed;
}

.task-list li:last-child {
	border-bottom: none;
}

.task-list li.editing {
	border-bottom: none;
	padding: 0;
}


.task-list li.editing .view {
	display: none;
}

.task-list li.editing:last-child {
	margin-bottom: -1px;
}

.edit {
 	position: relative;
	width: calc(100% - 43px);
	padding: 12px 16px;
	margin: 0 0 0 43px;
	font-size: 24px;
	font-family: inherit;
	font-weight: 400;
	line-height: 1.4em;
	color: inherit;
	border: 1px solid #999;
	box-shadow: inset 0 -1px 5px 0 rgba(0, 0, 0, 0.2);
	box-sizing: border-box;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
